import React from "react";
import 또타 from "./img/또타.png";

const onSubmit = (event) => {
  event.preventDefault();
  const questions = document.getElementsByClassName("question");
  const answers = [
    "나는 파란S가 좋아요",
    "나는 또타가 좋아요",
    "나는 서울교통공사가 좋아요",
    "141230",
    "동력분산식",
    "전라선",
    "새마을호",
    "경인선, 1899년 09월 18일",
    "서울교통공사",
    "4",
  ];
  let wrongCount = 0;
  for (let i = 0; i < 10; i++) {
    if (questions[i].value !== answers[i]) {
      wrongCount++;
    }
  }
  if (wrongCount === 0) {
    document.getElementsByClassName("App")[0].innerHTML = "<h1>G2GR9ANC36</h1>";
    alert("모두 정답입니다!");
  } else {
    alert(`${wrongCount}개 답이 틀렸습니다!`);
  }
};

function App() {
  return (
    <div className="App">
      <img alt="또타의 사진입니다." src={또타} style={{ width: 200 }} />
      <div className="paragraph">
        모스부호를 해독해서 여기에 접속하시다니, 대단합니다.
      </div>
      <div className="paragraph">
        다음 질문들에 정확한 답을 적어 제출하시면 선물코드를 얻으실 수 있습니다!
        최대한 인터넷 검색을 하지말고 풀어주세요!
      </div>
      <br />
      <form id="questionForm" onSubmit={onSubmit}>
        <div className="paragraph">
          1. "나는 파란S가 좋아요" 라고 입력해주세요.
        </div>
        <input className="question" id="q1" />
        <div className="paragraph">
          2. "나는 또타가 좋아요" 라고 입력해주세요.
        </div>
        <input className="question" id="q2" />
        <div className="paragraph">
          3. "나는 서울교통공사가 좋아요" 라고 입력해주세요.
        </div>
        <input className="question" id="q3" />
        <div className="paragraph">
          4. 또타의 생년월일을 6글자로 입력해주세요.
        </div>
        <input className="question" id="q4" />
        <div className="paragraph">
          5. 동력집중식과 동력분산식 중 어떤 방식이 더 한국에 적합한가요?
        </div>
        <input className="question" id="q5" />
        <div className="paragraph">
          6. '익산-여수엑스포' 구간 선로 이름은 무엇인가요? (이는 구 선로에서 신
          선로로 바뀌며 여수역이 여수엑스포역으로 이름이 바뀌었습니다.)
        </div>
        <input className="question" id="q6" />
        <div className="paragraph">
          7. 2018년 4월 퇴역한 한국철도공사의 열차의 이름은 무엇인가요? (대체
          열차가 나오긴 했습니다.)
        </div>
        <input className="question" id="q7" />
        <div className="paragraph">
          8. 우리나라 최초의 철도와 그 개통일을 적어주세요 (OOO, YYYY년 MM월
          DD일)
        </div>
        <input className="question" id="q8" />
        <div className="paragraph">
          9. 서울메트로 + 서울도시철도는 무엇인가요?
        </div>
        <input className="question" id="q9" />
        <div className="paragraph">
          10. 일본 야마노테선 노선도에 있는 역이 아닌 것은? (숫자만)
        </div>
        <div className="paragraph">
          1. 시나가와 2. 이케부쿠로 3. 신바시 4. 교토 5. 니시닛포리
        </div>
        <input className="question" id="q10" />
        <input type="submit" />
      </form>
      <h1 id="presentCode"> </h1>
    </div>
  );
}

export default App;
